@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&family=Pacifico&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{line-height:1.4;font-family:'Lato', sans-serif;background-color:#260036}.CodeMirror{margin:auto;margin-top:2.5rem;max-width:1100px;min-height:80vh;border-radius:5px}.loader{border:7px solid #f3f3f3;border-radius:50%;border-top:7px solid #ed1365;width:50px;height:50px;-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite;margin:auto}.loaderContainer{margin-top:2rem}@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.Header{background:linear-gradient(to right, #ad5389, #3c1053)}.Header__container{max-width:1100px;margin:auto;padding:0.5rem}.Header__h2{font-size:2rem;font-family:'Pacifico', cursive;background:-webkit-linear-gradient(#a8ff78, #78ffd6);-webkit-background-clip:text;-webkit-text-fill-color:transparent;text-decoration:none}.folders{max-width:1100px;margin:auto;margin-top:2.5rem;display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:1rem}.skeleton{background-image:-webkit-gradient(linear, left bottom, left top, color-stop(0.33, #bc4e9c), color-stop(0.67, #f80759));border-radius:5px;-webkit-animation:back-change 2s infinite;animation:back-change 2s infinite;-webkit-animation-direction:alternate;animation-direction:alternate;margin-top:2rem}.skeleton__img{width:100%;height:100%}@-webkit-keyframes back-change{from{opacity:1}to{opacity:0.5}}@keyframes back-change{from{opacity:1}to{opacity:0.5}}.card{display:flex;background-color:#333;min-height:312px;border-radius:5px;flex-direction:column;margin-top:2rem;transition:transform ease-in-out 0.2s;cursor:pointer}.card:hover{transform:scale(1.1)}.card__img{width:100%;flex:2 1}.card__name{color:white;flex:1 1;padding:1rem;font-size:1.5rem;background-color:black}.button{margin:auto;text-align:center;margin-top:2.5rem}.loadButton{padding:1rem;color:white;background-image:-webkit-gradient(linear, left bottom, left top, color-stop(0.33, #bc4e9c), color-stop(0.67, #f80759));border:none;cursor:pointer;font-size:1.25rem;border-radius:3px}.filesList{max-width:1100px;margin:auto}.files{padding:1rem;background-image:-webkit-gradient(linear, left bottom, right bottom, color-stop(0.33, #ad5389), color-stop(0.67, #3c1053));border-bottom:#3c1053 solid 1px;transition:opacity ease-in-out 0.2s}.files:hover{opacity:0.7}.files:nth-child(1){margin-top:2.5rem}.fileName{color:white;background:-webkit-linear-gradient(#a8ff78, #78ffd6);-webkit-background-clip:text;-webkit-text-fill-color:transparent}

